import React from "react";
import styles from "./roomlist.module.scss";
import { Link } from "react-router-dom";

function gettoday() {
  var today = new Date().getDay();
  if (today === 0 || today === 6) return true;
  return false;
}

export const roomlist = props => {
  return (
    <div className={styles["room"]}>
      {Object.getOwnPropertyNames(props.roominfo).length !== 0 &&
        props.roominfo.items.map((data, index) => {
          return (
            <Link
              to={{
                pathname: "/f2e6/roominfo/",
                state: {
                  roomid: data.id
                }
              }}
              className={styles["roomlist"]}
              key={index}
            >
              <div className={styles["top"]}>
                <img src={data.imageUrl} alt="roomlist" />
              </div>
              <div className={styles["bottom"]}>
                <p>{data.name}</p>
                <span
                  className={
                    styles["normalDayPrice"] +
                    " " +
                    (!gettoday() && styles["focus"])
                  }
                >
                  {"NT." + data.normalDayPrice}
                  <span>平日</span>
                </span>
                <span
                  className={
                    styles["holidayPrice"] +
                    " " +
                    (gettoday() && styles["focus"])
                  }
                >
                  {"NT." + data.holidayPrice}
                  <span>假日</span>
                </span>
              </div>
            </Link>
          );
        })}
    </div>
  );
};
