import React, { Component } from "react";
import "./calendar.scss";

export default class calendar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      month_olympic: [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
      month_normal: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    };
  }

  componentDidMount() {
    this.holder = document.getElementById("days");
    this.prev = document.getElementById("prev");
    this.next = document.getElementById("next");
    this.ctitle = document.getElementById("calendar-title");
    this.cyear = document.getElementById("calendar-year");

    this.my_date = new Date();
    this.my_year = this.my_date.getFullYear();
    this.my_month = this.my_date.getMonth();
    this.my_day = this.my_date.getDate();
    this.handleRefreshDate();
  }

  handleRefreshDate() {
    this.str = "";
    this.totalDay = this.handleDayMonth(this.my_month, this.my_year); //获取该月总天数
    this.firstDay = this.handleDayStart(this.my_month, this.my_year); //获取该月第一天是星期几
    this.myclass = "";
    this.bookdata = this.props.bookdata;

    for (let i = 0; i < this.bookdata.length; i++) {
      this.temp = new Date(this.bookdata[i].date);
      this.bookdata[i].year = this.temp.getFullYear();
      this.bookdata[i].month = this.temp.getMonth();
      this.bookdata[i].day = this.temp.getDate();
    }

    for (let i = 0; i < this.firstDay; i++) {
      this.str += "<li></li>"; //为起始日之前的日期创建空白节点
    }
    for (let i = 1; i <= this.totalDay; i++) {
      if (
        (i < this.my_day &&
          this.my_year === this.my_date.getFullYear() &&
          this.my_month === this.my_date.getMonth()) ||
        this.my_year < this.my_date.getFullYear() ||
        (this.my_year === this.my_date.getFullYear() &&
          this.my_month < this.my_date.getMonth())
      ) {
        this.myclass = " class='lightgrey'"; //当该日期在今天之前时，以浅灰色字体显示
      } else {
        this.myclass = " class='darkgrey'"; //当该日期在今天之后时，以深灰字体显示
      }
      for (let j = 0; j < this.bookdata.length; j++) {
        if (
          i === this.bookdata[j].day &&
          this.my_year === this.bookdata[j].year &&
          this.my_month === this.bookdata[j].month
        )
          this.myclass = " class='green greenbox'"; //当天日期以绿色背景突出显示
      }
      this.str += "<li" + this.myclass + ">" + i + "</li>"; //创建日期节点
    }
    this.holder.innerHTML = this.str; //设置日期显示
    this.ctitle.innerHTML = this.my_month + 1; //设置英文月份显示
    this.cyear.innerHTML = this.my_year; //设置年份显示
  }

  componentDidUpdate() {
    this.handleRefreshDate();
  }

  handleDayStart(month, year) {
    var tmpDate = new Date(year, month, 1);
    return tmpDate.getDay();
  }

  handleDayMonth(month, year) {
    if (year % 4 === 0 && (year % 100 !== 0 || year % 400 === 0)) {
      return this.state.month_olympic[month];
    } else {
      return this.state.month_normal[month];
    }
  }

  handlePrev(e) {
    e.preventDefault();
    this.my_month--;
    if (this.my_month < 0) {
      this.my_year--;
      this.my_month = 11;
    }
    this.handleRefreshDate();
  }

  handleNext(e) {
    e.preventDefault();
    this.my_month++;
    if (this.my_month > 11) {
      this.my_year++;
      this.my_month = 0;
    }
    this.handleRefreshDate();
  }

  render() {
    return (
      <div className={"calendar"}>
        <div className={"title"}>
          <div className={"titleflex"}>
            <div className={"green small"} id="calendar-year">
              Year
            </div>
            {" / "}
            <div className={"green"} id="calendar-title">
              Month
            </div>
          </div>
          <div className={"prev"} onClick={e => this.handlePrev(e)} id="prev">
            ❮
          </div>
          <div className={"next"} onClick={e => this.handleNext(e)} id="next">
            ❯
          </div>
        </div>
        <div className={"body"}>
          <div className={"lightgrey body-list"}>
            <ul>
              <li>SUN</li>
              <li>MON</li>
              <li>TUE</li>
              <li>WED</li>
              <li>THU</li>
              <li>FRI</li>
              <li>SAT</li>
            </ul>
          </div>
          <div className={"darkgrey body-list"}>
            <ul id="days" />
          </div>
        </div>
      </div>
    );
  }
}
