import React, { Component } from "react";
import { Link } from "react-router-dom";
import { getallrooms, postroomsdata } from "../api";
import Loading from "../component/loading";
import Calendar from "../component/calendar";

import styles from "./roominfo.module.scss";
import "./roominfo.scss";

import Logo from "../img/logo_block.svg";
import Wifilogo from "../img/wifi.svg";
import Phonelogo from "../img/phone.svg";
import Viewlogo from "../img/mountain-range.svg";
import Breakfastlogo from "../img/breakfast.svg";
import Breezelogo from "../img/breeze.svg";
import Smokelogo from "../img/no-smoke-symbol.svg";
import Barlogo from "../img/bar.svg";
import Refrigerator from "../img/refrigerator.svg";
import Chilelogo from "../img/crawling-baby-silhouette.svg";
import Servicelogo from "../img/room_service.svg";
import Sofa from "../img/Sofa.svg";
import Petlogo from "../img/dog.svg";
import Successlogo from "../img/success.svg";

export default class roominfo extends Component {
  constructor(e) {
    super(e);
    this.state = {
      loading: true,
      amenities: {},
      checkInAndOut: {},
      description: "",
      descriptionShort: {},
      holidayPrice: 0,
      id: "",
      photo: [],
      name: "",
      normalDayPrice: 0,
      bookdata: [],
      totalNday: 0,
      totalHday: 0,
      total: 0,
      scrolling: false,
      step: 0
    };
    this.handleNofocus = this.handleNofocus.bind(this);
    this.handleChooseDay = this.handleChooseDay.bind(this);
    this.Counttotal = this.Counttotal.bind(this);
    this.handleImSureCheck = this.handleImSureCheck.bind(this);
    this.handleCancle = this.handleCancle.bind(this);
    this.lightboxtime = 0;
  }

  componentDidMount() {
    var reactthis = this;
    getallrooms(function(value) {
      reactthis.setState({
        amenities: value.room[0].amenities,
        checkInAndOut: value.room[0].checkInAndOut,
        description: value.room[0].description,
        descriptionShort: value.room[0].descriptionShort,
        holidayPrice: value.room[0].holidayPrice,
        id: value.room[0].id,
        photo: value.room[0].imageUrl,
        name: value.room[0].name,
        normalDayPrice: value.room[0].normalDayPrice,
        bookdata: value.booking,
        loading: false
      });
    }, this.props.location.state.roomid);
    // window.addEventListener("click", this.handleNofocus);
    this.self = document.getElementById("reservebox");
  }

  //確認api輸入資料
  handleCheckdata(type) {
    return Object.getOwnPropertyNames(type).length !== 0;
  }

  //關閉輸入表單的函式
  handleCancle(e) {
    this.setState({
      step: 0
    });
    if (e === true) {
      return;
    }
    var reactthis = this;
    getallrooms(function(value) {
      reactthis.setState({
        amenities: value.room[0].amenities,
        checkInAndOut: value.room[0].checkInAndOut,
        description: value.room[0].description,
        descriptionShort: value.room[0].descriptionShort,
        holidayPrice: value.room[0].holidayPrice,
        id: value.room[0].id,
        photo: value.room[0].imageUrl,
        name: value.room[0].name,
        normalDayPrice: value.room[0].normalDayPrice,
        bookdata: value.booking,
        loading: false
      });
    }, this.state.id);
    var checkbtn = document.getElementById("reservecheck");
    checkbtn.click();
  }

  //關閉輸入表單
  handleNofocus(e) {
    if (this.self === e.target) {
      this.handleCancle();
    }
  }

  //計算平日假日總天數
  handleChooseDay() {
    this.startday = document.getElementById("startday");
    this.finilyday = document.getElementById("finilyday");
    if (this.startday.value && this.finilyday.value) {
      this.dateStartday = new Date(this.startday.value);
      this.datefinilyday = new Date(this.finilyday.value);
      this.daylength =
        this.datefinilyday.getTime() - this.dateStartday.getTime();
      if (this.daylength < 0) {
        alert("請選擇正確日期(開始~結束)");
        this.startday.value = null;
        this.finilyday.value = null;
        return;
      }
      this.totalday = this.daylength / 1000 / 60 / 60 / 24;
      if (this.totalday === 0) {
        if (
          this.dateStartday.getDay() === 0 ||
          this.dateStartday.getDay() === 5 ||
          this.dateStartday.getDay() === 6
        ) {
          this.Counttotal(0, 1);
          return;
        }
        this.Counttotal(1, 0);
      }
      this.totalday++;
      this.tempWeek = Math.floor(this.totalday / 7);
      if (!this.totalday % 7) {
        this.Counttotal(this.tempWeek * 4, this.tempWeek * 3);
        return;
      }

      this.tempNday = 0;
      this.tempHday = 0;

      for (let i = 0; i < this.totalday % 7; i++) {
        this.whatsday = new Date();
        this.whatsday.setDate(this.dateStartday.getDate() + i);
        if (
          this.whatsday.getDay() === 0 ||
          this.whatsday.getDay() === 5 ||
          this.whatsday.getDay() === 6
        )
          this.tempHday++;
        else this.tempNday++;
      }

      this.Counttotal(
        this.tempWeek * 4 + this.tempNday,
        this.tempWeek * 3 + this.tempHday
      );

      return;
    }
    return;
  }

  //總價格計算
  Counttotal(normalday, holiady) {
    this.total =
      normalday * this.state.normalDayPrice + holiady * this.state.holidayPrice;
    this.setState({
      totalHday: holiady,
      totalNday: normalday,
      total: this.total
    });
  }

  //確認輸入資料正確
  handleImSureCheck() {
    var reactthis = this;
    this.username = document.getElementById("username");
    this.telphone = document.getElementById("telphone");
    this.startday = document.getElementById("startday");
    this.finilyday = document.getElementById("finilyday");
    this.username.classList.remove("error");
    this.telphone.classList.remove("error");
    this.startday.classList.remove("error");
    this.finilyday.classList.remove("error");

    if (!this.username.value) {
      this.username.classList.add("error");
      return;
    }
    if (!this.telphone.value) {
      this.telphone.classList.add("error");
      return;
    }
    if (!this.startday.value) {
      this.startday.classList.add("error");
      return;
    }
    if (!this.finilyday.value) {
      this.finilyday.classList.add("error");
      return;
    }
    this.totaldayArr = [];
    this.dateStartday = new Date(this.startday.value);
    this.datefinilyday = new Date(this.finilyday.value);
    this.daylength = this.datefinilyday.getTime() - this.dateStartday.getTime();
    this.totalday = this.daylength / 1000 / 60 / 60 / 24;

    for (let i = 0; i < this.totalday + 1; i++) {
      this.whatsday = new Date();
      this.whatsday.setDate(this.dateStartday.getDate() + i);
      if (this.whatsday.getMonth() < 10) {
        this.totaldayArr.push(
          this.whatsday.getFullYear() +
            "-0" +
            (this.whatsday.getMonth() + 1) +
            "-" +
            this.whatsday.getDate()
        );
      } else {
        this.totaldayArr.push(
          this.whatsday.toLocaleDateString().replace(/\//g, "-")
        );
      }
    }

    postroomsdata(
      function(e) {
        if (!!e.success && e.success) {
          reactthis.setState({
            step: 1
          });
          return;
        }
        reactthis.setState({
          step: 2
        });
      },
      this.state.id,
      this.username.value,
      this.telphone.value,
      this.totaldayArr
    );
  }

  handlelightbox(e) {
    this.lightlist = document.getElementById("lightlist");
    if (e) {
      this.lightboxtime++;
    } else {
      this.lightboxtime--;
    }
    if (this.lightboxtime > 2) {
      this.lightboxtime = 0;
    } else if (this.lightboxtime < 0) {
      this.lightboxtime = 2;
    }
    for (let i = 0; i < 3; i++) {
      this.lightlist.classList.remove("lightbox-choose-" + i);
    }
    this.lightlist.classList.add("lightbox-choose-" + this.lightboxtime);
  }
  render() {
    var State = this.state;
    return (
      <>
        {/* 讀取中 */}
        <Loading loading={State.loading} />

        {/* 返回首頁 */}
        <Link to="/f2e6/" className={styles["Logo"]}>
          <img src={Logo} alt="LogoBlock" />
        </Link>

        {/* 照片牆 */}
        <div className={styles["photowall"]}>
          <label htmlFor="lightboxCK" className={styles["left"]}>
            <img
              src={State.photo.length > 0 ? State.photo[0] : null}
              alt="photowall"
            />
          </label>
          <label htmlFor="lightboxCK" className={styles["righttop"]}>
            <img
              src={State.photo.length > 0 ? State.photo[1] : null}
              alt="photowall"
            />
          </label>
          <label htmlFor="lightboxCK" className={styles["rightbottom"]}>
            <img
              src={State.photo.length > 0 ? State.photo[2] : null}
              alt="photowall"
            />
          </label>
        </div>

        {/* lightbox */}
        <input type="checkbox" name="lightboxCK" id="lightboxCK" />
        <div
          className={styles["lightbox"]}
          onClick={e => {
            if (e.target === document.getElementById("lightbox"))
              document.getElementById("lightboxCK").click();
          }}
          id="lightbox"
        >
          <div className={styles["lightslide"]}>
            <div className={styles["lightlist"]} id="lightlist">
              <div>
                <img
                  src={State.photo.length > 0 ? State.photo[0] : null}
                  alt="photowall"
                />
              </div>
              <div>
                <img
                  src={State.photo.length > 0 ? State.photo[1] : null}
                  alt="photowall"
                />
              </div>

              <div>
                <img
                  src={State.photo.length > 0 ? State.photo[2] : null}
                  alt="photowall"
                />
              </div>
            </div>
          </div>
          <div
            className={styles["lightprev"]}
            onClick={this.handlelightbox.bind(this, false)}
          >
            ❮
          </div>
          <div
            className={styles["lightnext"]}
            onClick={this.handlelightbox.bind(this, true)}
          >
            ❯
          </div>
        </div>

        {/* 詳細資訊 */}
        <div className={styles["main"]}>
          <div className={styles["left"]}>
            <div className={styles["roomdetail"]}>
              <h1>{!!State.name ? State.name : "What's Room"}</h1>
              {this.handleCheckdata.bind(this, State.descriptionShort) ? (
                <>
                  <p>
                    {"房客人數限制: " +
                      State.descriptionShort.GuestMin +
                      "~" +
                      State.descriptionShort.GuestMax +
                      " 人"}
                  </p>
                  <p>{"床型: " + State.descriptionShort.Bed}</p>
                  <p>
                    {"衛浴數量: " +
                      State.descriptionShort["Private-Bath"] +
                      " 間"}
                  </p>
                  <p>
                    {"房間大小: " +
                      State.descriptionShort.Footage +
                      " 平分公尺"}
                  </p>
                </>
              ) : (
                <>
                  <p>{"房客人數限制:0~0人"}</p>
                  <p>{"床型:no data"}</p>
                  <p>{"衛浴數量:0間"}</p>
                  <p>{"房間大小:0平分公尺"}</p>
                </>
              )}

              <div className={styles["descript"]}>
                {!!State.description ? State.description : "none"}
              </div>
              <div className={styles["slashbox"]}>
                <div className={styles["slash"]} />
                <div className={styles["slash"]} />
                <div className={styles["slash"]} />
              </div>
              <div className={styles["checktime"]}>
                {this.handleCheckdata.bind(this, State.checkInAndOut) ? (
                  <>
                    <div className={styles["checkin"]}>
                      {State.checkInAndOut.checkInEarly +
                        " — " +
                        State.checkInAndOut.checkInLate}
                    </div>
                    <div className={styles["checkout"]}>
                      {State.checkInAndOut.checkOut}
                    </div>
                  </>
                ) : (
                  <>
                    <div className={styles["checkin"]}>{"00:00 — 00:00"}</div>
                    <div className={styles["checkout"]}>{"00:00"}</div>
                  </>
                )}
              </div>

              <table className={styles["roomservice"]}>
                {this.handleCheckdata.bind(this, State.amenities) ? (
                  <>
                    <tbody>
                      <tr>
                        <td
                          className={
                            State.amenities["Wi-Fi"] ? styles["use"] : ""
                          }
                        >
                          <img src={Wifilogo} alt="wi-fi" />
                          Wi-Fi
                        </td>
                        <td
                          className={
                            State.amenities["Television"] ? styles["use"] : ""
                          }
                        >
                          <img src={Phonelogo} alt="tel" />
                          電話
                        </td>
                        <td
                          className={
                            State.amenities["Great-View"] ? styles["use"] : ""
                          }
                        >
                          <img src={Viewlogo} alt="nice view" />
                          漂亮的視野
                        </td>
                      </tr>
                      <tr>
                        <td
                          className={
                            State.amenities["Breakfast"] ? styles["use"] : ""
                          }
                        >
                          <img src={Breakfastlogo} alt="breakfast" />
                          早餐
                        </td>
                        <td
                          className={
                            State.amenities["Air-Conditioner"]
                              ? styles["use"]
                              : ""
                          }
                        >
                          <img src={Breezelogo} alt="Breeze" />
                          空調
                        </td>
                        <td
                          className={
                            State.amenities["Smoke-Free"] ? styles["use"] : ""
                          }
                        >
                          <img src={Smokelogo} alt="nosmoke" />
                          禁止吸菸
                        </td>
                      </tr>
                      <tr>
                        <td
                          className={
                            State.amenities["Mini-Bar"] ? styles["use"] : ""
                          }
                        >
                          <img src={Barlogo} alt="bar" />
                          Mini Bar
                        </td>
                        <td
                          className={
                            State.amenities["Refrigerator"] ? styles["use"] : ""
                          }
                        >
                          <img src={Refrigerator} alt="refrigerator" />
                          冰箱
                        </td>
                        <td
                          className={
                            State.amenities["Child-Friendly"]
                              ? styles["use"]
                              : ""
                          }
                        >
                          <img src={Chilelogo} alt="child" />
                          適合兒童
                        </td>
                      </tr>
                      <tr>
                        <td
                          className={
                            State.amenities["Room-Service"] ? styles["use"] : ""
                          }
                        >
                          <img src={Servicelogo} alt="service" />
                          Room Service
                        </td>
                        <td
                          className={
                            State.amenities["Sofa"] ? styles["use"] : ""
                          }
                        >
                          <img src={Sofa} alt="sofa" />
                          沙發
                        </td>
                        <td
                          className={
                            State.amenities["Pet-Friendly"] ? styles["use"] : ""
                          }
                        >
                          <img src={Petlogo} alt="Pet" />
                          寵物攜帶
                        </td>
                      </tr>
                    </tbody>
                  </>
                ) : null}
              </table>
            </div>
            <div className={styles["price"]}>
              <div className={styles["NormalPrice"]}>
                {"NT." + State.normalDayPrice}
                <div className={styles["pricetext"]}>平日(一~四)</div>
              </div>
              <div className={styles["HolidayPrice"]}>
                {"NT." + State.holidayPrice}
                <div className={styles["pricetext"]}>假日(五~日)</div>
              </div>
            </div>
          </div>
          <div className={styles["right"]}>
            <div className={styles["calendarbox"]}>
              <p>*斜線表示已預約</p>
              <Calendar bookdata={State.bookdata} />
            </div>
            <label htmlFor="reservecheck" className={styles["reserve"]}>
              預約時段
            </label>
          </div>
        </div>
        <input
          type="checkbox"
          className={styles["reservecheck"]}
          name="reservecheck"
          id="reservecheck"
        />
        <div
          className={styles["reservebox"]}
          id="reservebox"
          onClick={this.handleNofocus}
        >
          <div
            className={styles["reserve-box-Content"]}
            id="reserve-box-Content"
          >
            {State.step === 0 ? (
              <>
                {" "}
                {/* 資料輸入 */}
                <div className={styles["reserve-input"]}>
                  <h1>預約時段</h1>
                  <div className={styles["slashbox"]}>
                    <div className={styles["slash"]} />
                    <div className={styles["slash"]} />
                    <div className={styles["slash"]} />
                  </div>

                  <div
                    className={
                      styles["reserveinput"] + " " + styles["username"]
                    }
                  >
                    <input type="text" name="username" id="username" />
                  </div>

                  <div
                    className={
                      styles["reserveinput"] + " " + styles["telphone"]
                    }
                  >
                    <input type="tel" name="telphone" id="telphone" />
                  </div>

                  <div
                    className={
                      styles["reserveinput"] + " " + styles["reservetime"]
                    }
                  >
                    <input
                      type="date"
                      onChange={this.handleChooseDay}
                      name="startday"
                      id="startday"
                    />
                    <span>~</span>
                    <input
                      type="date"
                      onChange={this.handleChooseDay}
                      name="finilyday"
                      id="finilyday"
                    />
                  </div>
                </div>
                {/* 總天數 */}
                <div className={styles["totalday"]}>
                  <div className={styles["totalNday"]}>
                    <div>平日時段</div>
                    <div>{State.totalNday + "夜"}</div>
                  </div>
                  <div className={styles["totalHday"]}>
                    <div>假日時段</div>
                    <div>{State.totalHday + "夜"}</div>
                  </div>
                </div>
                {/* 總價格 */}
                <div className={styles["total"]}>{"=  NT." + State.total}</div>
                <div className={styles["totalcheck"]}>
                  <div>
                    <button
                      type="button"
                      className={
                        styles["ImNotSure"] + " " + styles["letsChoose"]
                      }
                      onClick={this.handleCancle}
                    >
                      取消
                    </button>
                  </div>
                  <div>
                    <button
                      type="button"
                      onClick={this.handleImSureCheck}
                      className={styles["ImSure"] + " " + styles["letsChoose"]}
                    >
                      確定預約
                    </button>
                  </div>
                </div>
              </>
            ) : State.step === 1 ? (
              <>
                <div
                  className={styles["reserve-input"] + " " + styles["sccuess"]}
                >
                  <h1>預約成功</h1>
                  <div className={styles["slashbox"]}>
                    <div className={styles["slash"]} />
                    <div className={styles["slash"]} />
                    <div className={styles["slash"]} />
                  </div>
                  <img src={Successlogo} alt="Success" />
                  <div className={styles["resultbutton"]}>
                    <button onClick={this.handleCancle}>回頁面</button>
                  </div>
                </div>
              </>
            ) : (
              <>
                <div className={styles["reserve-input"] + " " + styles["fail"]}>
                  <h1>預約失敗</h1>
                  <div className={styles["slashbox"]}>
                    <div className={styles["slash"]} />
                    <div className={styles["slash"]} />
                    <div className={styles["slash"]} />
                  </div>
                  <p>預約時間已被人預約</p>
                  <div className={styles["resultbutton"]}>
                    <button onClick={this.handleCancle.bind(this, true)}>
                      返回
                    </button>
                  </div>
                </div>
              </>
            )}
          </div>
        </div>
      </>
    );
  }
}
