import React from "react";
import styles from "./loading.module.scss";

const loading = props => {
  return (
    <div className={props.loading ? styles["loading"] : null}>
      <div className={styles["color"]} />
      <div className={styles["color"]} />
      <div className={styles["color"]} />
      <div className={styles["color"]} />
    </div>
  );
};
export default loading;
