import React, { Component } from "react";
import { getallrooms } from "./api";
import { roomlist as Roomlist } from "./component/roomlist";
import roominfo from "./component/roominfo";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Loading from "./component/loading";

import Logo_white from "./img/logo_white.svg";
import Logo_fb from "./img/facebook-square-brands.svg";
import Logo_ig from "./img/instagram-brands.svg";
import "./App.scss";

class Home extends Component {
  constructor(e) {
    super(e);
    this.state = {
      data: {},
      loading: true
    };
  }

  componentDidMount() {
    var reactthis = this;
    if (Object.getOwnPropertyNames(this.state.data).length !== 0) {
      return;
    }

    getallrooms(function(value) {
      reactthis.setState({
        data: value,
        loading: false
      });
    });

    this.Carouseltime = 1;
    this.car = document.getElementById("Carousel");
    this.Carousel = setInterval(function() {
      if (reactthis.Carouseltime > 5) {
        reactthis.Carouseltime = 0;
      }
      reactthis.car.classList.value = "Carousel";
      reactthis.car.classList.add("choose-" + reactthis.Carouseltime);
      reactthis.Carouseltime++;
    }, 5000);
  }

  componentWillUnmount() {
    clearInterval(this.Carousel);
  }

  handleClick(e) {
    console.log(Object.getOwnPropertyNames(e.state.data).length === 0);
  }

  render() {
    var State = this.state;
    return (
      <>
        <Loading loading={this.state.loading} />
        <div className="Carousel" id="Carousel">
          <ul>
            {Object.getOwnPropertyNames(State.data).length !== 0 &&
              State.data.items.map((roomdata, index) => (
                <li key={index}>
                  <img src={roomdata.imageUrl} alt="roomphoto" />
                </li>
              ))}
          </ul>
          <div className="HomeContent">
            <div className="Homeflex">
              <div className="HomeTop">
                <img src={Logo_white} alt="logo" />
              </div>
              <div className="HomeBottom">
                <div className="left">
                  <img src={Logo_ig} alt="logo" />
                  <img src={Logo_fb} alt="logo" />
                </div>
                <div className="right">
                  <p>02-1234567</p>
                  <p>Whitespace@whitespace.com.tw</p>
                  <p>台北市的深山裡</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <Roomlist roominfo={this.state.data} />
      </>
    );
  }
}

export default class App extends Component {
  render() {
    return (
      <Router>
        <Route path="/f2e6/" exact component={Home} />
        <Route path="/f2e6/roominfo/" component={roominfo} />
      </Router>
    );
  }
}
