import Axios from "axios";

export function getallrooms(callback, id) {
  if (id) {
    Axios({
      method: "get",
      url: "https://challenge.thef2e.com/api/thef2e2019/stage6/room/" + id,
      headers: {
        Authorization:
          "Bearer uGgMerZ8HTve2skPF3h257rTHfJTPWPZlkpkFz69qY06EI06JRvNPwDgxvKj",
        Accept: "application/json"
      }
    })
      .then(res => {
        if (res.status >= 200 && res.status < 300) {
          callback(res.data);
        }
      })
      .catch(error => {
        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx
          console.log(error.response.data);
          console.log(error.response.status);
          console.log(error.response.headers);
        } else if (error.request) {
          // The request was made but no response was received
          // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
          // http.ClientRequest in node.js
          console.log(error.request);
        } else {
          // Something happened in setting up the request that triggered an Error
          console.log("Error", error.message);
        }
        console.log(error.config);
      });
    return;
  }

  Axios({
    method: "get",
    url: "https://challenge.thef2e.com/api/thef2e2019/stage6/rooms",
    headers: {
      Authorization:
        "Bearer uGgMerZ8HTve2skPF3h257rTHfJTPWPZlkpkFz69qY06EI06JRvNPwDgxvKj",
      Accept: "application/json"
    }
  })
    .then(res => {
      if (res.status >= 200 && res.status < 300) {
        callback(res.data);
        return;
      }
    })
    .catch(error => {
      if (error.response) {
        // The request was made and the server responded with a status code
        // that falls out of the range of 2xx
        console.log(error.response.data);
        console.log(error.response.status);
        console.log(error.response.headers);
      } else if (error.request) {
        // The request was made but no response was received
        // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
        // http.ClientRequest in node.js
        console.log(error.request);
      } else {
        // Something happened in setting up the request that triggered an Error
        console.log("Error", error.message);
      }
      console.log(error.config);
    });
}

export function postroomsdata(callback, id, username, telphone, totalday) {
  let data = { name: username, tel: telphone, date: totalday };

  Axios({
    method: "post",
    url: "https://challenge.thef2e.com/api/thef2e2019/stage6/room/" + id,
    headers: {
      Authorization:
        "Bearer uGgMerZ8HTve2skPF3h257rTHfJTPWPZlkpkFz69qY06EI06JRvNPwDgxvKj",
      Accept: "application/json"
    },
    data: data
  })
    .then(res => {
      if (res.status >= 200 && res.status < 300) {
        callback(res.data);
      }
    })
    .catch(error => {
      if (error.response) {
        // The request was made and the server responded with a status code
        // that falls out of the range of 2xx
        callback(error.response);
      } else if (error.request) {
        // The request was made but no response was received
        // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
        // http.ClientRequest in node.js
        callback(error.request);
      } else {
        // Something happened in setting up the request that triggered an Error
        console.log("Error", error.message);
      }
      console.log(error.config);
    });
  return;
}
